@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-padding-top: 70px;
}
.shadow-bottom {
  box-shadow: 0 6px 6px -6px rgba(0, 0, 0, 0.2);
}

a,
input,
button {
  touch-action: manipulation;
}

.highlight-darkblue::selection {
  background: #2b3951;
}

.animate-listening {
  animation: listening 1s linear infinite;
}

@keyframes listening {
  0% {
    color: red;
  }
  50% {
    color: black;
  }
  100% {
    color: red;
  }
}

@keyframes listening-dark {
  0% {
    color: red;
  }
  50% {
    color: white;
  }
  100% {
    color: red;
  }
}

.dark body {
  color-scheme: dark;
}

.dark .text-gray-500 {
  color: rgb(160 162 167);
}

.dark .animate-listening {
  animation: listening-dark 1s linear infinite;
}

.dark hr {
  border-color: #555;
}

@media print {
  .hide-when-print {
    display: none !important;
  }
}

.react-tooltip {
  max-width: 300px;
  z-index: 10;
}

.animate-wave {
  animation: wave 1s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes wave {
  0%,
  100% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(-10deg);
  }
}
.bottom-arrow:after {
  content: ' ';
  position: absolute;
  right: 50%;
  bottom: -8px;
  border-top: 8px solid #5ec269;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
  border-bottom: none;
}

.org-welcome-message ul {
  text-align: left;
}
img.error-fallback-gray:before {
  content: ' ';
  display: block;
  height: 100%;
  width: 100%;
  background: #cccccc;
}

@supports (-webkit-touch-callout: none) {
  .h-screen {
    height: 100vh;
    max-height: -webkit-fill-available !important;
  }
}
